/* Panel */
.cat-ui-dropdown-panel {
  position: absolute;
  background-color: var(--cat-ui-bg-dropdown-panel);
  z-index: 1;
  top: 10px;
  right: 10px;
  height: 30px;
  width: 30px;
  overflow: hidden;
  opacity: 0;
  border-radius: 0 0 5px 5px;
  /* transition: background-color 0.1s, width 0.2s, height 0.2s; */
}
.cat-ui-dropdown-panel-right {
  left: 0;
  right: initial;
}
.cat-ui-dropdown-panel-borderradius {
  border-radius: 15px;
}

.cat-ui-dropdown-panel > div {
  margin: 10px;
}

.cat-ui-dropdown-panel-open {
  width: calc(100% - 20px);
  height: auto;
  top: 50px;
  opacity: 1;
  animation: cat-ui-dropdown-panel-show-left 0.5s ease-in;
}
.cat-ui-dropdown-panel-open-right {
  animation: cat-ui-dropdown-panel-show-right 0.5s ease-in;
}
.cat-ui-dropdown-panel-closing {
  animation: cat-ui-dropdown-panel-hide-left 0.5s ease-in reverse;
}
.cat-ui-dropdown-panel-closing-right {
  animation: cat-ui-dropdown-panel-hide-right 0.5s ease-in reverse;
}

.cat-ui-dropdown-panel-open > div {
  opacity: 1;
  animation: cat-ui-dropdown-panel-inner-show 0.5s ease-in;
}
.cat-ui-dropdown-panel-closing > div {
  opacity: 0;
  animation: cat-ui-dropdown-panel-inner-hide 0.5s ease-in reverse;
}

@keyframes cat-ui-dropdown-panel-show-left {
  0% {
    top: 10px;
    right: 10px;
    height: 30px;
    width: 30px;
    opacity: 0;
    border-radius: 15px;
  }
  50% {
    width: 30px;
    height: 30px;
    top: 40px;
    right: 5px;
    opacity: 0.8;
    border-radius: 15px;
  }
  75% {
    width: 100px;
    height: 100px;
    top: 80px;
    right: 5px;
    opacity: 0.8;
  }
  100% {
    width: calc(100% - 20px);
    height: auto;
    right: 10px;
    top: 50px;
    opacity: 1;
  }
}
@keyframes cat-ui-dropdown-panel-hide-left {
  0% {
    top: 10px;
    right: 10px;
    height: 30px;
    width: 30px;
    opacity: 0;
    border-radius: 15px;
