body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root, body, html {
  height: 100%;
}

.loader {
  margin: auto;
  height: 320px;
  width: 320px;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.flex-row {
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: row;
  height: 100%;
  width: 100%;
  overflow: hidden;
  display: flex;
  flex-wrap: wrap;
}

.flex-inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 5px;
  color: white;
}

.flex-inner h1 {
  font-size: 16px;
  margin: 5px;
  padding: 0;
}

.flex-inner p {
  font-size: 14px;
  margin: 5px;
  padding: 0;
}

.flex-outer {
  width: 100%;
  height: 40;
  background-color: #26a69a;
  z-index: 10000;
}

.flex-rest {
  position: relative;
  width: 100%;
  height: calc(100% - 40px);
}